/* eslint-disable no-unused-vars */
import React, { useState } from 'react'
import { useLocation } from 'react-router-dom'
import { Box, ThemeProvider } from '@mui/material'
import theme from '../../../utils/themeMUI5'
import Filters from '../../../components/Sub/Filters/Filters'
import useStyles from './ConsultantMetricsPage.style'
import useFilters from '../../../hooks/useFilters'
import { AchievementsList, GraphsSlider, WeekStats, WeekTasks } from '../../../components/Main/MetricsPage'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import { getStats } from '../../../services/axios/metrics'
import { resetMetrics } from '../../../services/slices/metrics-slice'
import { resetAchievements } from '../../../services/slices/achievements-slice'
import { getAchievements } from '../../../services/axios/achievements'

const StudentsDiaryPage = ({ setSnack }) => {
  const dispatch = useAppDispatch()
  const [filtersJSON] = useState({})
  const location = useLocation()
  const { studentId, studentName } = location.state ?? {}
  const hook = useFilters({ id: studentId, name: studentName })
  const { auth, metrics } = useAppSelector((state) => state)
  const { profile } = auth
  const { eventsStats, funnelStats } = metrics

  const stats =
    profile?.status === 'Сообщество' ? (
      <WeekStats data={profile?.student_metrics_stats} />
    ) : (
      <WeekTasks data={profile?.student_metrics_stats} />
    )

  React.useEffect(() => {
    if (profile?.id) {
      dispatch(getStats(profile?.id))
      dispatch(getAchievements(profile?.id))
    }
    return () => {
      dispatch(resetMetrics())
      dispatch(resetAchievements())
    }
  }, [profile?.id])

  const classes = useStyles()
  return (
    <ThemeProvider theme={theme}>
      <Box component="main" className={classes.root}>
        <Box sx={{ gridColumn: '1 / 3' }}>
          <Filters filterType="metrics" hook={hook} filtersJSON={filtersJSON} />
        </Box>
        <GraphsSlider funnelStats={funnelStats} eventsStats={eventsStats} />
        {stats}
        <Box sx={{ gridColumn: '1 / 3' }}>
          <AchievementsList />
        </Box>
      </Box>
    </ThemeProvider>
  )
}

export default React.memo(StudentsDiaryPage)
